import React from "react";
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { Link } from 'gatsby';

const NavLink = ({ to, children }) => (
    <li className='nav-item'>
        <Link to={to} className='nav-link'>{children}</Link>
    </li>
);

const ExternalLink = ({ to, children }) => (
    <li className='nav-item'>
        <a href={to} className='nav-link'>{children}</a>
    </li>
);

const Header = () => (
    <Navbar bg="dark" variant="dark" expand="lg" sticky='top'>
        <Link className='navbar-brand' to="/">EasyOlMaps</Link>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="pageNavbar">
            <Nav className="mr-auto">
                <NavLink to='/examples'>Examples</NavLink>
                <NavLink to='/apidoc'>API</NavLink>
                <ExternalLink to='https://github.com/henryvanner/easyolmaps'>Code</ExternalLink>
            </Nav>
        </Navbar.Collapse>
    </Navbar>
);

export default ({ children }) => (
    <React.Fragment>
        <Header />
        {children}
    </React.Fragment>
)